    --primary: #faa328;
    --fnt: #fff;
    --hvr: #695d4f;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
    text-decoration: none;
}

body {
    background-color: var(--fnt);
    color: black;
}

/* Skip Link */
.skip-link {
    position: absolute;
    left: -9999px;
}

.skip-link:focus {
    position: fixed;
    left: 10px;
    top: 10px;
    z-index: 9999;
    background-color: #fff;
    padding: 10px;
    border: 2px solid #000;
}
/* Skip Link */

/* 
    App-Bar Start */
/* App-Bar */
.app-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 5%;
    background-color: var(--primary);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    box-shadow: 0px 5px 15px rgba(0, 0, 5, 0.8);
}

/* App-Bar-Brand */
.app-bar .app-brand h1 {
    font-family: "Mochiy Pop One", sans-serif;
    color: var(--fnt);
}

.app-bar .app-brand h1 span {
    color: var(--hvr);
}

/* Navbar */
.navbar a {
    display: inline-block;
    margin: 0 1rem;
    font-family: "Playpen Sans", cursive;
    font-weight: 600;
    font-size: 1.8rem;
    color: var(--fnt);
}

.navbar a:hover {
    color: var(--hvr);
    transition: 0.2s;
}

.navbar a::after {
    content: "";
    display: block;
    border-bottom: 2px solid var(--hvr);
    transform: scaleX(0);
    transition: 0.2s linear;
}

.navbar a:hover::after {
    transform: scaleX(0.5);
}

/* Hamburger-Menu */
.nav-extra {
    display: none;
}

.nav-extra button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    border: none;
    color: var(--fnt);
    background-color: var(--primary);
}

.nav-extra button:hover {
    color: var(--primary);
    background-color: #695d4f;
    border-radius: 10px;
    transition: 0.2s;
}

/* Focus-Mode */
.navbar a:focus {
    color: var(--hvr);
    transition: 0.2s;
}

.navbar a:focus::after {
    transform: scaleX(0.5);
}

.nav-extra button:focus {
    color: var(--primary);
    background-color: #695d4f;
    border-radius: 10px;
    transition: 0.2s;
}
/* 
    App-Bar End */

/* 
    Jumbotron Start */
.hero {
    display: flex;
    height: 380px;
    background-image: linear-gradient(rgba(0, 0, 5, 0.8), rgba(0, 0, 5, 0.5)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-position: center;
    align-items: center;
    justify-content: center;
    color: white;
}

.hero .heroInner {
    font-family: "Playpen Sans", cursive;
    font-size: 2rem;
    text-align: center;
}

.hero h3 {
    letter-spacing: 5px;
}
/* 
    Jumbotron End */

/* 
    Main Content Start*/
main {
    padding: 30px;
}

.content-header {
    padding: 0 6px;
    margin-bottom: 1rem;
    text-align: center;
    font-family: "Playpen Sans", cursive;
    color: var(--fnt);
    background-color: var(--primary);
}
/* 
    Main Content End */

/* 
    Content Tamplate Start */
.content .restaurants-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.restaurant-title {
    height: 450px;
    width: 400px;
    padding: 5px;
    margin: 5px;
    border-radius: 20px;
    background-color: #fff;
    box-shadow: 0px 5px 15px rgba(0, 0, 5, 0.2);
}

.restaurant-item-header p {
    position: absolute;
    color: var(--fnt);
    background-color: var(--primary);
    padding: 5px 10px 5px 10px;
    border-radius: 15px 0 15px 0;
}

.restaurant-item-header img {
    height: 250px;
    width: 100%;
    border-radius: 15px;
}

.restaurant-title .restaurant-item-header .restaurant-rating {
    display: inline-block;
    padding: 0 0.5rem 0 0.1rem;
    border-radius: 30px;
    background-color: var(--hvr);
    color: white;
}

.restaurant-item-content h3 a {
    font-size: 1.8rem;
    font-family: "Playpen Sans", cursive;
    color: black;
    cursor: pointer;
}

.restaurant-item-content p {
    text-align: justify;
}
/* 
    Content Tamplate End */

/* 
    Detail Template Start */
.restaurant-detail {
    padding: 5px;
    border-radius: 15px;
    background-color: #fff;
    box-shadow: 0px 5px 15px rgba(0, 0, 5, 0.2);
}

.detail-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.detail-header h2 {
    width: 100%;
    margin-bottom: 1rem;
    text-align: center;
    font-family: "Playpen Sans", cursive;
    font-size: 2rem;
    border-radius: 10px 10px 0 0;
    background-color: var(--primary);
    color: var(--fnt);
}

.detail-header img {
    height: 500px;
    padding: 3px;
    margin-bottom: 1rem;
    border: 2px solid var(--hvr);
    border-radius: 15px;
}

.detail-info {
    font-size: 1.3rem;
}

.detail-info h3 {
    text-align: center;
    font-family: "Playpen Sans", cursive;
    color: var(--fnt);
    background-color: var(--primary);
}

.address h4 {
    color: var(--hvr);
}

.detail-menu {
    font-size: 1.3rem;
}

.detail-menu h3 {
    margin-bottom: 5px;
    text-align: center;
    font-family: "Playpen Sans", cursive;
    color: var(--fnt);
    background-color: var(--primary);
}

.food {
    padding: 5px;
    margin-bottom: 1rem;
    border-radius: 10px;
    background-color: rgba(105, 93, 79, 0.2);
    box-shadow: 0px 5px 15px rgba(0, 0, 5, 0.2);
}

.food h4 {
    text-align: center;
    color: rgb(77, 68, 58);
}

.drink {
    padding: 5px;
    margin-bottom: 1rem;
    border-radius: 10px;
    background-color: rgba(105, 93, 79, 0.2);
    box-shadow: 0px 5px 15px rgba(0, 0, 5, 0.2);
}

.drink h4 {
    text-align: center;
    color: rgb(77, 68, 58);
}

.detail-review-header {
    margin-bottom: 5px;
    text-align: center;
    font-family: "Playpen Sans", cursive;
    color: var(--fnt);
    background-color: var(--primary);
}

.review-title {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    padding: 0.5rem 2rem;
}

.review-item {
    padding: 20px 22px;
    border: 1px solid var(--primary);
    border-radius: 10px;
    box-shadow: 0px 5px 15px rgba(0, 0, 5, 0.2);
    color: black;
}

.restaurant-detail .review-item_bold {
    font-weight: bold;
}

.restaurant-detail .review-item_bold {
    font-weight: bold;
}

.detail-description h3 {
    font-family: "Playpen Sans", cursive;
    text-align: center;
    color: var(--fnt);
    background-color: var(--primary);
}

.detail-description p {
    text-align: justify;
}

/* 
    Detail Template End */

/*
    Like */
.like {
    width: 55px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #db0000;

    position: fixed;
    bottom: 16px;
    right: 16px;
    border-radius: 50%;
    border: 0;

    font-size: 18px;
    color: white;
    cursor: pointer;
}

/* Footer Start */
footer {
    padding: 1rem;
}

footer p {
    text-align: center;
    color: #aaaaaa;
}

footer p span {
    color: var(--primary);
}
/* Footer End */
